import React, {Component} from 'react';


export default class contentTool extends Component {
    createContent(){
        let {generateObj} = this.props.tools;
        let content;
        for(let obj in generateObj){
            content +=(
                <div>
                    {obj}
                </div>
            );
        }
        return content;
    }

    render() {

        let content, temp;
        switch (this.props.tools.currentItem) {
            case 'create':
                temp = 'contentToolContainer';
                let {generateObj} = this.props.tools;
                for(let obj in generateObj){
                    content +=(
                        <div>
                            {obj}
                        </div>
                );
            }
                break;
        }


        return (
            <div className={temp}>
                {content}
            </div>
        );
    }
}
